import { RichText, useBlockProps } from '@wordpress/block-editor';
import classnames from 'classnames';
import './style.scss';
const agentImage = require('./images/user.jpg');

export default function save({ attributes }) {
	const {
		buttonSize,
		borderRadius,
		buttonType,
		text,
		info,
		title,
		online,
		offline,
		textAlignment,
		buttonLinkTarget,
		visibility,
		border,
		iconTarget,
		imageUrl,
		whatsappType,
		numberInput,
		groupInput,
		prefilledMessageInput,
		timeZone,
		mondayStart,
		mondayEnd,
		tuesdayStart,
		tuesdayEnd,
		wednesdayStart,
		wednesdayEnd,
		thursdayStart,
		thursdayEnd,
		fridayStart,
		fridayEnd,
		saturdayStart,
		saturdayEnd,
		sundayStart,
		sundayEnd,
		topPadding,
		rightPadding,
		bottomPadding,
		leftPadding,
	} = attributes;
	const textClasses = classnames(`wHelpButtons-align-${textAlignment}`);
	const basicBtn = classnames(
		`chat_help_analytics wHelp-button-4 wHelp-btn-bg`
	);
	const classes = classnames(
		`wHelpButtons wHelp-button-4 wHelp-btn-bg`
	);

	return (
		<>
			{buttonType === 'basic-button' ? (
				<div className={`button-wrapper ${textClasses}`}>
					<a
						{...useBlockProps.save({
							className: `${basicBtn} ${buttonSize} ${borderRadius} ${visibility} ${border}`,
						})}
						style={{
							'--padding': `${topPadding}px ${rightPadding}px ${bottomPadding}px ${leftPadding}px`,
						}}
						{...(whatsappType === 'number'
							? { 'data-number': numberInput }
							: { 'data-group': groupInput })}
						href={
							whatsappType === 'number'
								? `https://wa.me/${numberInput}?text=${prefilledMessageInput}`
								: `${groupInput}`
						}
						rel="noopener noreferrer"
						target={buttonLinkTarget ? '_blank' : '_self'}
					>
						{iconTarget && (
							<span className="dashicons dashicons-whatsapp"></span>
						)}
						<RichText.Content tagName="span" value={text} />
					</a>
				</div>
			) : (
				<div className={`button-wrapper ${textClasses}`}>
					<div
						{...useBlockProps.save({
							className: `${classes} ${buttonSize} ${borderRadius} ${visibility}`,
						})}
						style={{
							'--padding': `${topPadding}px ${rightPadding}px ${bottomPadding}px ${leftPadding}px`,
						}}
						data-btnavailablety={`{ "monday":"${mondayStart}-${mondayEnd}", "tuesday":"${tuesdayStart}-${tuesdayEnd}", "wednesday":"${wednesdayStart}-${wednesdayEnd}", "thursday":"${thursdayStart}-${thursdayEnd}", "friday":"${fridayStart}-${fridayEnd}", "saturday":"${saturdayStart}-${saturdayEnd}", "sunday":"${sundayStart}-${sundayEnd}" }`}
						data-timezone={timeZone}
						{...(whatsappType === 'number'
							? { 'data-number': numberInput }
							: { 'data-group': groupInput })}
					>
						<img
							src={imageUrl ? imageUrl : agentImage}
							alt="agent"
						/>
						<div className="info-wrapper">
							<RichText.Content
								value={info}
								tagName="p"
								className="info"
							/>
							<RichText.Content
								value={title}
								tagName="p"
								className="title"
							/>
							<RichText.Content
								value={online}
								tagName="p"
								className="online"
							/>
							<RichText.Content
								value={offline}
								tagName="p"
								className="offline"
							/>
						</div>
						<a
							href={
								whatsappType === 'number'
									? `https://wa.me/${numberInput}?text=${prefilledMessageInput}`
									: `${groupInput}`
							}
							rel="noopener noreferrer"
							target={buttonLinkTarget ? '_blank' : '_self'}
						></a>
					</div>
				</div>
			)}
		</>
	);
}
